/*Oswald Webfont*/
@import url("https://fonts.googleapis.com/css?family=Oswald:300,400,700");
@font-face {
  font-family: 'Oswald', sans-serif; }

.container {
  width: 100%;
  margin-bottom: 40px;
  display: flex;
  justify-content: center; }

.poster-spring {
  position: relative;
  width: 300px;
  height: 0;
  padding-top: 135%; }
  @media (min-width: 600px) {
    .poster-spring {
      width: 100%; } }
  @media (min-width: 1026px) {
    .poster-spring {
      width: 100%; } }
  .poster-spring > div {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
